<template>
    <div>
        <table border="2">
            <caption>简易伤害估算器</caption>
            <!-- 攻击力 -->
            <tr>
                <td style="width:20%">攻击力（人物面板）</td>
                <td style="width:80%">
                    总攻击力（白字+绿字）<input v-model="zgj"/>
                    <!-- 白字攻击<input v-model="bzgj"/>
                    绿字攻击<input v-model="lzgj"/> -->
                </td>
            </tr>
            <!-- <tr>
 
                <td style="width:20%">攻击力</td>
                <td style="width:80%">
                    <p v-if="warn1" style="color:red">人物白字、武器白字不能为0</p>
                    人物白字<input v-model="rwbz"/>
                    人物百分比<input v-model="rwbfb"/>%
                    <br>
                    武器白字<input v-model="wqbz"/>
                    武器百分比<input v-model="wqbfb"/>%
                    <br>
                    圣遗物固定<input v-model="sywgd"/>
                    圣遗物百分比<input v-model="sywbfb"/>%
                </td>
            </tr> -->
            <!-- 元素、物理增伤 -->
            <tr>
                <td>元素/物理增伤(包括角色、武器、圣遗物、组队效果、圣遗物套装)</td>
                <td>
                    元素/物理伤害增加<input v-model="shzj"/>%
                    <!-- 总元素伤害增加<input v-model="ysshzj"/>%
                    物理伤害增加<input v-model="wlshzj"/>% -->
                </td>
            </tr>
            <!-- 倍率 -->
            <tr>
              
                <td>技能倍率</td>
                <td>
                      <p v-if="warn2" style="color:red">技能倍率不能为0</p>
                    倍率<input v-model="bl"/>%
                </td>
            </tr>
            <!-- 爆伤 -->
            <tr>
               
                <td>爆伤(面板的包尚志，默认50%)</td>
                <td>
                     <p v-if="warn3" style="color:red">爆伤最低为50%</p>
                    爆伤<input v-model="bs"/>%
                </td>
            </tr>
            <!-- 其他加成 -->
            <tr>
                <td>其他加成</td>
                <td>
                    <!-- 攻击力<input v-model="other_gjl"/>% -->
                    总伤害提升<input v-model="other_shts"/>%
                </td>
            </tr>
            <!-- 结果 -->
            <tr>
                <td>结果</td>
                <td>
                    <!-- 白字伤害：{{this.result1}} -->
                    <!-- 物理伤害:{{this.result3}} -->
                    伤害：{{this.result2}}
                </td>
            </tr>
            <tr>
                <td colspan="2">
                    <el-button type="success" @click="confirm">确定</el-button>
                </td>
            </tr>
        </table>
        <p>邮箱：930217906@qq.com欢迎讨论。更便携、详细的计算器还在开发中。</p>
        <p>数据提升比较暂无设计，请自行观察。</p>
    </div>
</template>
<script>
export default {
    data (){
        return {
            zgj : 0,//总攻击
            // bzgj : 0,//白字攻击
            // lvgj : 0,//绿字攻击
            // rwbz:0,//人物白字
            // rwbfb:0,//人物百分比
            // wqbz:0,//武器白字
            // wqbfb:0,//武器百分比
            // sywgd:0,//圣遗物固定
            // sywbfb:0,//圣遗物百分比
            shzj : 0,//物理/元素伤害增加
            ysshzj:0,//元素伤害增加
            wlshzj:0,//物理伤害增加
            bl:0,//倍率
            bs:50,//爆伤
            other_gjl:0,//其他攻击力加成
            other_shts:0,//其他伤害提升
            result1:0,//白字伤害
            result2:0,//元素伤害
            result3:0,//物理伤害
            warn1:false,
            warn2:false,
            warn3:false,
        }
    },
    methods : {
        confirm (){
            // if(this.rwbz==0||this.wqbz==0){this.warn1=true}
            // else{this.warn1=false}
            if(this.zgj==0){this.warn1=true}
            else{this.warn1=false}
            if(this.bl==0){this.warn2=true}
            else{this.warn2=false}
            if(this.bs==0||this.bs<50){this.warn3=true}
            else{this.warn3=false}

            this.result2 =(((this.zgj)*this.bl/100*(1+this.shzj/100)*(1+this.bs/100))*(1+this.other_shts/100)).toFixed(0)
            // this.result2=((this.rwbz+this.wqbz)*(1+this.rwbfb/100+this.wqbfb/100+this.sywbfb/100+
            //     this.other_gjl/100)+this.sywgd) * (1+this.ysshzj/100+this.other_shts/100)*(1+this.bs/100) *
            //     this.bl/100
            // this.result3=((this.rwbz+this.wqbz)*(1+this.rwbfb/100+this.wqbfb/100+this.sywbfb/100+
            //     this.other_gjl/100)+this.sywgd) * (1+this.wlshzj/100+this.other_shts/100)*(1+this.bs/100) *
            //     this.bl/100
            // this.result1=((this.rwbz+this.wqbz)*(1+this.rwbfb/100+this.wqbfb/100+this.sywbfb/100+
            //     this.other_gjl/100)+this.sywgd) * (1+this.other_shts/100)*(1+this.bs/100) *
            //     this.bl/100    
        }
    },
    watch : {
    }
}
</script>
<style scoped>
@import url('../assets/A.css');
</style>


