import Vue from 'vue'
import Router from 'vue-router'
import B from '@/components/B'
import A from '@/components/A'
import C from '@/components/C'
import D from '@/components/D'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect:'/home'
    },
    {
      path: '/home',
      name :'Home',
      component: Home,
      children: [
        {
          path: '/a',
          name: 'A',
          component: A    
        },
        {
          path: '/b',
          name: 'B',
          component: B   
        },
        {
          path: '/c',
          name: 'C',
          component: C    
        },
        {
          path: '/d',
          name: 'D',
          component: D
        }
      ]
    }
  ]
})



