<template>
  <div>
    <table id="Table" border="2">
      <caption><p>不支持%形式的小数，请自觉改成0.123形式。谢谢！</p></caption>
      <tr>
        <td>基础属性</td>
        <td>
          <table>
            <tr><td>生命值上限</td><td><el-input type="number" v-model="jcsx.smzsx"></el-input></td></tr>
            <tr><td>攻击力</td><td><el-input type="number" v-model="jcsx.gjl"></el-input></td></tr>
            <tr><td>防御力</td><td><el-input type="number" v-model="jcsx.fyl"></el-input></td></tr>
            <tr><td>元素精通</td><td><el-input type="number" v-model="jcsx.ysjt"></el-input></td></tr>
            <tr><td style="font-size:12px"><p >蒸发、融化反应伤害提升  <b style="color:red">{{jcsx.add.a}}%</b></p>
            <br><p>超载、超导、感电、碎冰、扩散伤害提升  <b style="color:red">{{jcsx.add.b}}%</b></p>
            <br><p>结晶护盾的吸收量提升     <b style="color:red">{{jcsx.add.c}}%</b></p></td></tr>
          </table>
        </td>
      </tr>
      <tr>
        <td>进阶属性</td>
        <td>
          <table>
            <tr><td>暴击率</td><td><el-input type="number" v-model.number="jjsx.bjl"></el-input></td></tr>
            <tr><td>暴击伤害</td><td><el-input type="number" v-model="jjsx.bjsh"></el-input></td></tr>
            <tr><td>治疗加成</td><td><el-input type="number" v-model="jjsx.zljc"></el-input></td></tr>
            <tr><td>受治疗加成</td><td><el-input type="number" v-model="jjsx.szljc"></el-input></td></tr>
            <tr><td>元素充能效率</td><td><el-input type="number" v-model="jjsx.yscnxl"></el-input></td></tr>
            <tr><td>冷却缩减</td><td><el-input type="number" v-model="jjsx.lqsj"></el-input></td></tr>
            <tr><td>护盾强效</td><td><el-input type="number" v-model="jjsx.hdqx"></el-input></td></tr>
          </table>
        </td>
      </tr>
      <tr>
        <td>元素属性</td>
        <td>
          <table id="ysTable">
            <tr>
              <td>
                <table>
                   <tr><td>火伤加成</td><td><el-input type="number" v-model="yssx.hysshjc"></el-input></td></tr>
                   <tr><td>火抗</td><td><el-input type="number" v-model="yssx.hk"></el-input></td></tr>
                </table>
              </td>
              <td>
                <table>
                   <tr><td>水伤加成</td><td><el-input type="number" v-model="yssx.sysshjc"></el-input></td></tr>
                   <tr><td>水抗</td><td><el-input type="number" v-model="yssx.sk"></el-input></td></tr>
                </table>
              </td>
            </tr>
            <tr>
              <td>
                <table>
                   <tr><td>冰伤加成</td><td><el-input type="number" v-model="yssx.bysshjc"></el-input></td></tr>
                   <tr><td>冰抗</td><td><el-input type="number" v-model="yssx.bk"></el-input></td></tr>
                </table>
              </td>
              <td>
                <table>
                   <tr><td>雷伤加成</td><td><el-input type="number" v-model="yssx.lysshjc"></el-input></td></tr>
                   <tr><td>雷抗</td><td><el-input type="number" v-model="yssx.lk"></el-input></td></tr>
                </table>
              </td>
            </tr>
            <tr>
              <td>
                <table>
                   <tr><td>风伤加成</td><td><el-input type="number" v-model="yssx.fysshjc"></el-input></td></tr>
                   <tr><td>风抗</td><td><el-input type="number" v-model="yssx.fk"></el-input></td></tr>
                </table>
              </td>
              <td>
                <table>
                   <tr><td>草伤加成</td><td><el-input type="number" v-model="yssx.cysshjc"></el-input></td></tr>
                   <tr><td>草抗</td><td><el-input type="number" v-model="yssx.ck"></el-input></td></tr>
                </table>
              </td>
            </tr>
            <tr>
              <td>
                <table>
                   <tr><td>物伤加成</td><td><el-input type="number" v-model="yssx.wlshjc"></el-input></td></tr>
                   <tr><td>物抗</td><td><el-input type="number" v-model="yssx.wk"></el-input></td></tr>
                </table>
              </td>
              <td>
                <table>
                   <tr><td>岩伤加成</td><td><el-input type="number" v-model="yssx.yysshjc"></el-input></td></tr>
                   <tr><td>岩抗</td><td><el-input type="number" v-model="yssx.yk"></el-input></td></tr>
                </table>
              </td>
            </tr>
           
          </table>
        </td>
      </tr>
      <tr>
        <td>技能倍率</td>
        <td><el-input type="number" v-model="jnbl"></el-input></td>
      </tr>
      <tr>
        <td rowspan="2">反应区（增伤反应：融化、蒸发）</td>
        <td><p>科普下：蒸发(水打火)200%增伤；蒸发(火打水)150%增伤；融化(火打冰)200%增伤；融化(冰打火)150%增伤。<br>（因为水克火，火克冰）</p></td>
      </tr>
      <tr>
        <td>
          <el-radio-group v-model="fyq">
            <el-radio :label="1" border size="medium">蒸发（水打火）</el-radio>
            <br>
            <el-radio :label="2" border size="medium">蒸发（火打水）</el-radio>
            <br>
            <el-radio :label="3" border size="medium">融化（火打冰）</el-radio>
            <br>
            <el-radio :label="4" border size="medium">融化（冰打火）</el-radio>
            <br>
            <el-radio :label="5" border size="medium">无</el-radio>
            <br>
          </el-radio-group>
        </td>
      </tr>
      <tr>
        <td>其他增伤（比如逆飞4、角斗士4）</td>
        <td>
          <el-input type="number" v-model="qt"></el-input>
        </td>
      </tr>
      <tr>
        <td>怪物抗性、防御</td>
        <td>
          怪物抗性(参照抗性表)<el-input type="number" v-model="gwkx"></el-input><br>
          怪物防御减伤（一般为50%）<el-input type="number" v-model="gwfy"></el-input><br>
        </td>
      </tr>    
      <tr>
        <td><el-button @click="confirm" type="success">计算伤害</el-button></td>
        <td>
          暴击伤害{{result}} 
        </td>
      </tr>
      <tr>
        <td colspan="2">
          注意：
          <ul>
            <li>将百分比数转为小数，否则运算结果错误</li>
            <li>如果存在减抗、减防，直接在怪物抗性、防御一行中进行减算</li>
          </ul>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  data(){
      return{
          //角色基础属性
          jcsx : {
            smzsx : 0,
            gjl : 1,
            fyl : 0,
            ysjt : 0,
            add : {
              a : 0,// 0.0019 * ysjt  
              b : 0,//0.0045  * ysjt
              c : 0//0.0030  * ysjt
            }
          },
          //角色进阶属性
          jjsx : {
            bjl : 0.05,
            bjsh : 0.5,
            zljc : 0,
            szljc : 0,
            yscnxl : 1,
            lqsj : 0,
            hdqx : 0
          },
          //角色元素属性
          yssx : {
            hysshjc : 0,
            hk : 0,
            sysshjc : 0,
            sk : 0,
            cysshjc : 0,
            ck : 0,
            lysshjc : 0,
            lk : 0,
            fysshjc : 0,
            fk : 0,
            bysshjc : 0,
            bk : 0,
            yysshjc : 0,
            yk : 0,
            wlshjc : 0,
            wk : 0,
          },
          fyq : 5,//增伤反应类型
          jnbl : 1,//技能倍率
          qt : 0,//其他增伤
          gwkx: 0,//怪物抗性
          gwfy: 0.5,//怪物防御减伤
          result : 0,//纯输出结果
          result2: 0,//考虑抗性、防御的结果
          jkxg: 0,//减抗效果
      }
  },
  computed : {
    ysjtCompute(){
      return this.jcsx.ysjt
    }
  },
  methods:{
    confirm(){
      var a = this.jcsx.gjl //攻击力区
      var b = this.jnbl //倍率区
      var c = this.yssx.hysshjc*1+this.yssx.sysshjc*1+this.yssx.bysshjc*1+this.yssx.lysshjc*1+this.yssx.fysshjc*1+this.yssx.cysshjc*1
        +this.yssx.yysshjc*1+this.yssx.wlshjc*1+
        this.qt*1+1//增伤区
      var d = this.jjsx.bjsh*1+1//爆伤区
      var f = this.jcsx.add.a/100  //元素精通加成
      var e = 0 //增伤反应区
      if(this.fyq=='1'||this.fyq=='3'){
        e=3
      }
      else if(this.fyq=='2'||this.fyq=='4'){
        e=2.5
      }
      else{//无反应,不吃元素精通加成
        e=1
        f=0
      }
      console.log('攻击区：'+a+'\n'+
                  '增伤区：'+c+'\n'+
                  '倍率区：'+b+'\n'+
                  '爆伤区：'+d+'\n'+
                  '增伤反应区：'+(e+f)+'\n'
                  )
      // alert('攻击力区：'+a+'\n'+'增伤区：'+c+'\n'+'爆伤区'+d+'\n'+'增伤反应：'+e+'')
      this.result=(a*b*c*d*(e+f)*(1-this.gwkx)*(1-this.gwfy)).toFixed(0)
    }

  },
  watch:{
    ysjtCompute:{
      handler:function(){
        if(this.ysjtCompute==null){
          return
        }
        // console.log(this.yssx.hysshjc*1000)
        console.log(typeof(this.jcsx.ysjt))
        this.jcsx.add.a=(this.jcsx.ysjt*0.0019*100).toFixed(2)
        this.jcsx.add.b=(this.jcsx.ysjt*0.0045*100).toFixed(2)
        this.jcsx.add.c=(this.jcsx.ysjt*0.0030*100).toFixed(2)
      }
    }
  }
}
</script>

<style scoped>
@import url('../assets/B.css');
</style>


